$colors: (
  lightgray: #DCCECE,
  black: #1B1717,
  gray: #C4C4C4,
  border: #E5E5E5,
  btnHover: #554E4E,
);

@mixin color-modifiers {
  @each $name, $hex in $colors {
    &_color_#{$name} {
      color: $hex;
    }
  }
}

@mixin background-modifiers {
  @each $name, $hex in $colors {
    &_color_#{$name} {
      background-color: $hex;
    }
  }
}
